* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #ffe70c;
}

body .spTitle {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 50px auto auto;
  display: flex;
}

h2, h3 {
  font-family: Bebas Neue, cursive;
  font-size: 2em;
}

p {
  font-family: "Baloo Tammudu 2", cursive;
}

header {
  width: 100%;
  height: 100px;
  background-color: #000;
  justify-content: center;
  align-items: center;
  padding: 20px;
}

header .navbar_logo_div:hover {
  cursor: pointer;
}

nav {
  flex-flow: wrap;
  justify-content: space-between;
  align-items: center;
  font-family: "Baloo Tammudu 2", cursive;
  display: flex;
}

nav a {
  color: #ffe70c;
  text-decoration: none !important;
}

nav .navbar_search_div {
  background-color: #fff;
  padding: 5px;
  font-family: "Baloo Tammudu 2", cursive;
  display: flex;
}

nav .navbar__catDeals {
  flex-direction: row;
  align-items: center;
  list-style: none;
  display: flex;
}

nav .navbar_search {
  width: 25em;
  height: 25px;
  border: none;
}

nav .catDeals_item {
  margin: 0 40px;
}

nav .navbar__catDeals__categories {
  z-index: 2;
  padding: 10px 20px;
  list-style: none;
  display: none;
  position: absolute;
}

nav .navbar__cat__label {
  color: #ffe70c;
  margin: 0 2em;
  font-size: 1em;
}

nav .navbar__cat__label:hover {
  cursor: pointer;
  text-decoration: underline;
}

nav .navbar__cat__label:hover > .navbar__catDeals__categories {
  background-color: #ffe70c;
  border-radius: 10px;
  display: block;
}

nav .navbar__catDeals__newDeals {
  z-index: 2;
  padding: 10px 20px;
  list-style: none;
  display: none;
  position: absolute;
}

nav .navbar__addItems__label:hover {
  text-decoration: underline;
}

nav .navbar__deals__label {
  color: #ffe70c;
  margin: 0 2em;
  font-size: 1em;
}

nav .navbar__deals__label:hover {
  cursor: pointer;
  text-decoration: underline;
}

nav .navbar__deals__label:hover > .navbar__catDeals__newDeals {
  background-color: #ffe70c;
  border-radius: 10px;
  display: block;
}

nav .navbar_icons_div .navbar__icons {
  flex-direction: row;
  list-style: none;
  transition: transform .2s;
  display: flex;
}

nav .navbar_icons_div .navbar__icons_button {
  background: none;
  border: none;
  margin: 0 10px;
}

nav .navbar_icons_div .navbar__icons_button:hover {
  cursor: pointer;
  transform: scale(1.2);
}

nav .navbar_icons_div .navbar__icons #shopping_cart_button {
  margin-top: 1px;
  margin-right: 18px;
}

input {
  height: 20px;
}

.bombhead__logo {
  margin-right: 20px;
  transition: transform .2s;
  position: static;
}

.bombhead__logo:hover {
  transform: scale(1.2);
}

.burger_menu_div {
  list-style: none;
  display: none;
}

.burger_menu_div .menu_icon {
  width: 28px;
}

.burger_menu_div .navbar__icons_button {
  background: none;
  border: none;
}

.list_element {
  color: #000;
  text-decoration: none;
}

.list_element:hover {
  background-color: #d8c300;
}

